import { Wrapper } from "./Menu.styled";
import { Link } from "react-router-dom";

export const Menu = () => {
  return (
    <Wrapper>
      <ul>
        <li>
          <Link to={"/"}></Link>
        </li>
        <li>
          <Link to={"/about"}> </Link>
        </li>
        <li>
          <Link to={"/vacancies"}></Link>
        </li>
        <li>
          <Link to={"/contacts"}></Link>
        </li>
        <li>
          <Link to={"/questions"}>-</Link>
        </li>
      </ul>
    </Wrapper>
  );
};
